<template>
  <div class="allPage">
    <div class="core_container">
      <div class="form_container">
        <el-form
          :inline="true"
          :model="requestParam"
          class="demo-form-inline form_container_header"
          size="mini"
        >
          <el-form-item label="用户账号">
            <el-input v-model="requestParam.username"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="getUserList">查询</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="showDataTab">
        <el-button type="success" size="small" @click="showEdit('add', null)"
          >新增用户</el-button
        >
        <el-table
          :data="page.list"
          style="width: 100%"
          :row-class-name="tableRowClassName"
        >
          <el-table-column prop="uid" label="用户ID" align="center">
          </el-table-column>
          <el-table-column prop="username" label="账号" align="center">
          </el-table-column>
          <el-table-column prop="permission" label="权限等级" align="center">
          </el-table-column>
          <el-table-column label="用户状态" align="center">
            <template slot-scope="scope">
              <div>
                <span v-if="scope.row.status == 0">删除</span>
                <span v-else-if="scope.row.status == 1">正常</span>
                <span v-else>未知状态{{ scope.row.status }}</span>
              </div>
            </template>
          </el-table-column>
          <el-table-column label="操作" align="center">
            <template slot-scope="scope">
              <el-button
                type="primary"
                size="small"
                @click="showEdit('update', scope.row.username)"
                >编辑</el-button
              >
            </template>
          </el-table-column>
        </el-table>
        <el-dialog
          :title="action.title"
          :visible.sync="dialogFormVisible"
          show-close:true
        >
          <el-form
            :label-position="labelPosition"
            label-width="100px"
            :model="User"
            class="formStyle text_common"
            size="mini"
            ref="game"
          >
            <el-form-item label="账号" prop="username" :required="true">
              <el-input
                v-model.number="User.username"
                :disabled="action.type == 'update'"
              ></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="show_password">
              <el-input
                v-model="User.show_password"
                placeholder="不填则默认密码：123456"
              ></el-input>
            </el-form-item>
            <el-form-item label="状态" prop="status">
              <el-radio-group v-model.number="User.status" size="medium">
                <el-radio border :label="0">不可用</el-radio>
                <el-radio border :label="1">正常</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="权限等级" prop="permission">
              <el-input-number
                v-model="User.permission"
                :min="user.permission+1"
                :max="10"
                label="描述文字"
              ></el-input-number>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" size="medium" @click="commitUser"
                >提交</el-button
              >
              <el-button type="danger" size="medium" @click="resetForm('User')"
                >重置</el-button
              >
            </el-form-item>
          </el-form>
        </el-dialog>
      </div>
      <div class="pageBar">
        <el-pagination
          background
          hide-on-single-page
          layout="prev, pager, next"
          @next-click="nextPage"
          @current-change="thisPage"
          @prev-click="prePage"
          :current-page="page.current_page"
          :page-size="page.page_size"
          :total="page.total"
        ></el-pagination>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {},
  data() {
    return {
      labelPosition: "right",
      requestParam: {
        username: null,
        permission: this.$store.getters["user/getUser"].permission
      },
      User: {
        status: null,
        permission: null,
        _id: null,
        uid: null,
        username: null,
        password: null, //默认密码
        show_password: null
      },
      dialogFormVisible: false,
      responseData: [],
      page: {
        list: [],
        current_page: 1,
        total: 0,
        page_size: 8
      },
      action: {
        type: "",
        title: ""
      },
      user: this.$store.getters["user/getUser"]
    };
  },
  methods: {
    getUserList() {
      var that = this;
      that.responseData = [];
      this.$api.user.list(this.requestParam).then(res => {
        if (res.data.code === 200) {
          that.responseData = res.data.data;
          that.page.total = that.responseData.length;
          const start = (that.page.current_page - 1) * that.page.page_size;
          const end =
            that.page.total > that.page.current_page * that.page.page_size
              ? that.page.current_page * that.page.page_size
              : that.responseData.length;
          that.page.list = that.responseData.slice(start, end);
        } else {
          this.$message({
            showClose: true,
            message: "获取用户列表失败",
            type: "error"
          });
        }
      });
      //that.responseData = json.data.game;
    },
    showEdit(type, username) {
      this.resetUser();
      if (type == "add") {
        this.dialogFormVisible = true;
        this.action.type = type;
        this.action.title = "新增用户";
      } else {
        this.$api.user.list({ username: username }).then(res => {
          if (res.data.code === 200) {
            this.User = res.data.data[0];
          } else {
            this.$message({
              showClose: true,
              message: "获取失败,请重试!",
              type: "error"
            });
          }
        });
        this.dialogFormVisible = true;
        this.action.type = type;
        this.action.title = "更新用户";
      }
    },
    resetUser() {
      this.User = {
        status: null,
        permission: null,
        _id: null,
        uid: null,
        username: null,
        password: null
      };
    },
    commitUser() {
      if (this.action.type == "add") {
        this.addUser();
      } else {
        this.updateUser();
      }
    },
    addUser() {
      let that = this;
      this.User._id = null;
      this.User.uid = null;
      if (
        this.User.permission <= this.$store.getters["user/getUser"].permission
      ) {
        this.$message({
          showClose: true,
          message: "设置权限不合法",
          type: "error"
        });
        return;
      }
      this.User.show_password = this.User.show_password || "123456";
      this.User.password = this.$md5(this.User.show_password);
      this.$api.user.register(this.User).then(res => {
        if (res.data.code === 200) {
          this.$message({
            showClose: true,
            message: "新增成功",
            type: "success"
          });
          that.dialogFormVisible = false;
          that.resetUser();
        } else {
          this.$message({
            showClose: true,
            message: "新增失败",
            type: "error"
          });
        }
      });
    },
    updateUser() {
      let that = this;
      this.User.show_password = this.User.show_password.replace(/\s/g, "");
      if (
        this.User.permission >= this.$store.getters["user/getUser"].permission
      ) {
        this.$message({
          showClose: true,
          message: "设置权限不合法",
          type: "error"
        });
        return;
      }
      let commitjson = {};
      commitjson.permission = this.User.permission;
      if (this.User.show_password) {
        commitjson.password = this.$md5(this.User.show_password);
      }
      commitjson.status = this.User.status;
      commitjson.username = this.User.username;
      this.$api.user.update(commitjson).then(res => {
        if (res.data.code === 200) {
          this.$message({
            showClose: true,
            message: "修改成功",
            type: "success"
          });
          that.dialogFormVisible = false;
          that.resetUser();
        } else {
          this.$message({
            showClose: true,
            message: "修改失败",
            type: "error"
          });
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 4 === 1) {
        return "warning-row";
      } else if (rowIndex % 4 === 3) {
        return "success-row";
      }
      return "";
    },
    prePage() {
      if (this.page.current_page > 0) {
        this.page.current_page--;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    },
    thisPage(cpage) {
      this.page.current_page = cpage;
      this.page.list = this.responseData.slice(
        (this.page.current_page - 1) * this.page.page_size,
        this.responseData.length > this.page.current_page * this.page.page_size
          ? this.page.current_page * this.page.page_size
          : this.responseData.length
      );
    },
    nextPage() {
      if (
        this.page.current_page <
        Math.ceil(this.page.total / this.page.page_size)
      ) {
        this.page.current_page++;
        this.page.list = this.responseData.slice(
          (this.page.current_page - 1) * this.page.page_size,
          this.responseData.length >
            this.page.current_page * this.page.page_size
            ? this.page.current_page * this.page.page_size
            : this.responseData.length
        );
      }
    }
  }
  // mounted: function() {
  //   this.user = user;
  // }
};
</script>

<style lang="less" scoped>
.pageBar {
  width: 100%;
  height: 100px;
  // background-color: aquamarine;
  display: flex;
  justify-content: center;
  align-items: center;
}
.innerForm {
  padding: 2% 5%;
  border: #eef1f6 1px solid;
  margin-left: -100px;
  //box-shadow: #eef1f6 0px 0px 10px;
}
.innerForm_item {
  margin: 10px 0;
}
.dialog_input_small {
  width: 35%;
  margin: 0 20px;
}
.minInput {
  width: 40%;
}
</style>



